import React, { useState, useEffect } from 'react';
import { controlPanelStyles, mobileControlPanelStyles } from './styles';

const ControlPanel = ({
  botToken,
  setBotToken,
  botName, //   
  setBotName, //      
  onSaveToken,
  onSaveBotName, //     
  onSaveScenario,
  onDeleteSelected,
  onDeleteAll,
  onRunBot,
  onRestartBot,
  onStopBot, //    
  onNavigateBack,
  nodesCount,
  edgesCount,
  selectedCount,
  botId,
  isBotRunning, //   
  loadingStatus //   
}) => {
  const [showTokenInput, setShowTokenInput] = useState(false);
  const [showBotNameInput, setShowBotNameInput] = useState(false); //      
  const [showStats, setShowStats] = useState(false);
  const [isRestarting, setIsRestarting] = useState(false);
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);

  //    
  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 768);
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  //        
  const styles = isMobile ? mobileControlPanelStyles : controlPanelStyles;

  const handleRestart = async () => {
    if (typeof onRestartBot !== 'function') {
      console.error('onRestartBot is not a function');
      alert('  ');
      return;
    }

    setIsRestarting(true);
    try {
      await onRestartBot();
    } catch (error) {
      console.error('Error restarting bot:', error);
      alert('   ');
    } finally {
      setIsRestarting(false);
    }
  };

  const handleStop = async () => {
    if (typeof onStopBot !== 'function') {
      console.error('onStopBot is not a function');
      alert('  ');
      return;
    }

    try {
      await onStopBot();
    } catch (error) {
      console.error('Error stopping bot:', error);
      alert('   ');
    }
  };

  return (
    <div style={styles.panel}>
      {/*    */}
      <div style={{
        display: 'flex',
        alignItems: 'center',
        marginRight: '5px',
        padding: isMobile ? '3px 6px' : '4px 8px',
        borderRadius: '12px',
        background: isBotRunning ? '#28a745' : '#dc3545',
        color: 'white',
        fontSize: isMobile ? '10px' : '12px',
        fontWeight: 'bold'
      }} title={isBotRunning ? ' ' : ' '}>
        <div style={{
          width: isMobile ? '6px' : '8px',
          height: isMobile ? '6px' : '8px',
          borderRadius: '50%',
          background: 'white',
          marginRight: isMobile ? '4px' : '6px',
          animation: isBotRunning ? 'pulse 1.5s infinite' : 'none'
        }} />
        {isBotRunning ? 'ON' : 'OFF'}
      </div>

      {/*   */}
      <button onClick={onNavigateBack} style={{
        ...styles.button,
        ...styles.secondaryButton
      }} title="  ">
        
      </button>

      {/*  */}
      <button onClick={onDeleteSelected} style={{
        ...styles.button,
        ...styles.dangerButton
      }} title="     (Delete)">
        
      </button>

      <button onClick={onDeleteAll} style={{
        ...styles.button,
        ...styles.warningButton
      }} title="  ">
         ALL
      </button>

      {/*  */}
      <button onClick={onSaveScenario} style={{
        ...styles.button,
        ...styles.primaryButton
      }} title=" ">
        
      </button>

      {/*   */}
      <div style={{ position: "relative" }}>
        <button onClick={() => setShowTokenInput(!showTokenInput)} style={{
          ...styles.button,
          ...styles.successButton
        }} title=" ">
          
        </button>

        {showTokenInput && (
          <div style={styles.dropdown}>
            <input
              type="password"
              value={botToken}
              onChange={(e) => setBotToken(e.target.value)}
              placeholder=" "
              style={{
                width: "100%",
                padding: isMobile ? "4px" : "6px",
                border: "1px solid #ccc",
                borderRadius: "4px",
                marginBottom: "5px",
                fontSize: isMobile ? "10px" : "12px"
              }}
            />
            <button onClick={onSaveToken} style={{
              ...styles.button,
              ...styles.successButton,
              width: "100%"
            }}>
               
            </button>
          </div>
        )}
      </div>

      {/*    */}
      <div style={{ position: "relative" }}>
        <button onClick={() => setShowBotNameInput(!showBotNameInput)} style={{
          ...styles.button,
          ...styles.infoButton
        }} title="  ">
          
        </button>

        {showBotNameInput && (
          <div style={styles.dropdown}>
            <input
              type="text"
              value={botName}
              onChange={(e) => setBotName(e.target.value)}
              placeholder=" "
              style={{
                width: "100%",
                padding: isMobile ? "4px" : "6px",
                border: "1px solid #ccc",
                borderRadius: "4px",
                marginBottom: "5px",
                fontSize: isMobile ? "10px" : "12px"
              }}
            />
            <button onClick={onSaveBotName} style={{
              ...styles.button,
              ...styles.infoButton,
              width: "100%"
            }}>
               
            </button>
          </div>
        )}
      </div>

      {/*   */}
      <button
        onClick={onRunBot}
        disabled={loadingStatus || isBotRunning || !botToken}
        style={{
          ...styles.button,
          ...styles.infoButton,
          opacity: (loadingStatus || isBotRunning || !botToken) ? 0.6 : 1
        }}
        title={!botToken ? "  " : isBotRunning ? "  " : " "}
      >
        {loadingStatus ? '' : ''}
      </button>

      {/*   */}
      <button
        onClick={handleRestart}
        disabled={loadingStatus || !isBotRunning || !botToken}
        style={{
          ...styles.button,
          ...styles.warningButton,
          opacity: (loadingStatus || !isBotRunning || !botToken) ? 0.6 : 1
        }}
        title={!botToken ? "  " : !isBotRunning ? "  " : " "}
      >
        {isRestarting ? '' : ''}
      </button>

      {/*   */}
      <button
        onClick={handleStop}
        disabled={loadingStatus || !isBotRunning}
        style={{
          ...styles.button,
          ...styles.dangerButton,
          opacity: (loadingStatus || !isBotRunning) ? 0.6 : 1
        }}
        title={!isBotRunning ? "  " : " "}
      >
        {loadingStatus ? '' : ''}
      </button>

      {/*  */}
      <div style={{ position: "relative" }}>
        <button onClick={() => setShowStats(!showStats)} style={{
          ...styles.button,
          background: "#f8f9fa",
          color: "#6c757d",
          border: "1px solid #dee2e6"
        }} title="">
          
        </button>

        {showStats && (
          <div style={styles.dropdown}>
            <div>: {nodesCount}</div>
            <div>: {edgesCount}</div>
            <div>: {selectedCount}</div>
            <div>: {isBotRunning ? ' ' : ' '}</div>
            <div style={{marginTop: '5px', paddingTop: '5px', borderTop: '1px solid #eee'}}>
              ID: {botId}
            </div>
          </div>
        )}
      </div>

      {/*     */}
      <style>
        {`
          @keyframes pulse {
            0% { opacity: 1; }
            50% { opacity: 0.5; }
            100% { opacity: 1; }
          }
          
          /*     */
          ::-webkit-scrollbar {
            height: 6px;
          }
          
          ::-webkit-scrollbar-track {
            background: #f1f1f1;
          }
          
          ::-webkit-scrollbar-thumb {
            background: #c1c1c1;
            border-radius: 3px;
          }
          
          ::-webkit-scrollbar-thumb:hover {
            background: #a8a8a8;
          }
        `}
      </style>
    </div>
  );
};

export default ControlPanel;